.bdiv{
    width: 100%;
    height: 700px;
}

.bolder{
    font-weight:bold;
}

.italic{
    font-style: italic;
}

.underline{
    text-decoration: underline;
}

.textleft{
    text-align: left;
}

.textcenter{
    text-align: center;
}

.textright{
    text-align: right;
}

.boldBtn{
    border:1px solid black;
    background-color: rgb(239, 61, 29);
    color:white;
    font-family: sans-serif;
    border-radius: 5px;
    text-align: center;
    font-weight: bold;
}

.italicBtn{
    border:1px solid black;
    background-color: rgb(239, 61, 29);
    color:white;
    font-family: sans-serif;
    border-radius: 5px;
    text-align: center;
    padding-left:8.5px;
    padding-right:8.5px;
    font-style: italic;
}

.underlineBtn{
    border:1px solid black;
    background-color: rgb(239, 61, 29);
    color:white;
    font-family: sans-serif;
    border-radius: 5px;
    text-align: center;
    text-decoration: underline;
}

.leftBtn{
    background-color: white;
    border:none;
    border-radius: 5px;
}

.leftBtn:hover{
    background-color: rgb(233, 231, 231);
}

.leftBtn:focus{
    background-color: rgb(185, 255, 185);
}

.centerBtn{
    background-color: white;
    border:none;
    border-radius: 5px;
}

.centerBtn:hover{
    background-color: rgb(233, 231, 231);
}

.centerBtn:focus{
    background-color: rgb(185, 255, 185);
}

.rightBtn{
    background-color: white;
    border:none;
    border-radius: 5px;
}

.rightBtn:hover{
    background-color: rgb(233, 231, 231);
}

.rightBtn:focus{
    background-color: rgb(185, 255, 185);
}

.container .copyBtn{
    border:none;
    border-radius:7px;
    padding:3px 7px 7px 7px;
    background-color: white;
}

.container .copyBtn:hover{
    background-color: rgb(233, 231, 231);
}